*{
    margin: 0;
    padding: 0;
    color: #333;
    text-decoration: none;
    list-style: none;
    font-size: 14px;
}
h2{
    font-size: 20px;
    font-weight: 400;
}
.fl{float: left;}

.fr{float: right;}

.topBar_right ul li{
    display: inline-block;
    padding: 15px 30px;
}

.rule{
    font-size: 16px;
    height: 35px;
    line-height: 35px;
    font-weight: 700;
    text-indent:-1em; 
}

.content ul{
    width: 200px;
    /* border-bottom: 1px solid #ccc; */
    padding: 20px 30px;
}

.content ul li{
    text-align: left;
    height: 40px;
    line-height: 40px;
}

.first h2{
    text-align: center;
    font-size: 30px;
    height: 100px;
    line-height: 100px;
}
.text{
    position: absolute;
    top:80px;
    left: 250px;;
}


